import React, { useState, useMemo, useRef, useEffect } from "react";
import styled from "./BoardEdit.module.css";
import axios from "axios";
import { useNavigate, useParams } from "react-router-dom";
import "react-quill/dist/quill.snow.css";
import ReactQuill from "react-quill";

function BoardEdit() {
  const { sequenceNumber } = useParams(); // URL  sequenceNumber 
  const [text, setText] = useState("");
  const [title, setTitle] = useState("");
  const [password, setPassword] = useState(""); //   
  const [imageFiles, setImageFiles] = useState([]); //    
  const [existingImages, setExistingImages] = useState([]); //  
  const navigate = useNavigate();
  const quillRef = useRef(null);

  const API_BASE_URL = process.env.REACT_APP_API_BASE_URL;

  useEffect(() => {
    //    
    const fetchBoardData = async () => {
      try {
        const response = await axios.get(
          `${API_BASE_URL}/api/board/BoardDetail/${sequenceNumber}`
        );
        if (response.status === 200) {
          const { title, content, images } = response.data;
          setTitle(title);
          setText(content);
          setExistingImages(images || []);
        }
      } catch (error) {
        console.error("     :", error);
      }
    };

    fetchBoardData();
  }, [sequenceNumber]);

  const handleTitleChange = (event) => {
    setTitle(event.target.value);
  }; //   

  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
  }; //   

  const handleChange = (value) => {
    setText(value);
  }; //    

  //   
  const imageHandler = async () => {
    const input = document.createElement("input");
    input.setAttribute("type", "file");
    input.setAttribute("accept", "image/*");
    input.click();
    input.addEventListener("change", async () => {
      const file = input.files?.[0];

      if (file) {
        setImageFiles((prevFiles) => [...prevFiles, file]); //    
        const reader = new FileReader();
        reader.onload = () => {
          const editor = quillRef.current.getEditor();
          const range = editor.getSelection();
          editor.insertEmbed(range.index, "image", reader.result); //   
        };
        reader.readAsDataURL(file);
      } else {
        console.error("  .");
      }
    });
  };

  //      
  const modules = useMemo(() => {
    return {
      toolbar: {
        container: [
          ["image"],
          [{ header: [1, 2, 3, 4, 5, false] }],
          ["bold", "underline"],
        ],
        handlers: {
          image: imageHandler,
        },
      },
    };
  }, []);

  //   
  const handleUpdate = async () => {
    if (!password) {
      alert(" .");
      return;
    }

    //   <p>  
    const ptageBlock = text.replace(/<\/?p>/g, "");

    console.log("Updating data:", {
      title,
      content: ptageBlock,
      images: imageFiles,
      password,
    }); //     

    // FormData     
    const formData = new FormData();

    //   JSON  FormData 
    const postData = {
      title: title,
      content: ptageBlock,
    };
    formData.append(
      "data",
      new Blob([JSON.stringify(postData)], { type: "application/json" })
    );

    //  FormData 
    formData.append("password", password);

    // Quill      FormData 
    imageFiles.forEach((file) => {
      formData.append("images", file); //   images  
    });

    try {
      const response = await axios.put(
        `${API_BASE_URL}/api/board/update/${sequenceNumber}`,
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        }
      );
      console.log("Response:", response.data);
      alert(" .");
      navigate("/BoardList");
    } catch (error) {
      console.error("Error updating content:", error);
      alert("   .");
    }
  };
  //   

  return (
    <div id={styled.BoardEdit_wrap}>
      <form
        className={styled.in_editor_form}
        onSubmit={(e) => {
          e.preventDefault();
          handleUpdate();
        }}
      >
        <div className={styled.title_info_titles}>
          <span></span>&nbsp;&nbsp;
          <input
            type="text"
            className={styled.in_editor_titleInput}
            value={title}
            onChange={handleTitleChange}
          />
        </div>

        <div>
          <ReactQuill
            ref={quillRef}
            className={styled.in_editor}
            value={text}
            onChange={handleChange}
            modules={modules}
          />
        </div>

        <div className={styled.password_input_wrapper}>
          <span></span>&nbsp;&nbsp;
          <input
            type="password"
            className={styled.password_input}
            value={password}
            onChange={handlePasswordChange}
          />
        </div>

        <button type="submit" className={styled.saveButton}>
           
        </button>
      </form>
    </div>
  );
}

export default BoardEdit;
