import React, { useState, useMemo, useRef } from "react";
import styled from "./BoardSave.module.css";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import "react-quill/dist/quill.snow.css";
import ReactQuill from "react-quill";

function BoardSave() {
  const [text, setText] = useState("");
  const [title, setTitle] = useState("");
  const [imageFiles, setImageFiles] = useState([]);
  const [password, setPassword] = useState(""); //  
  const navigate = useNavigate();
  const quillRef = useRef(null);

  const handleTitleChange = (event) => setTitle(event.target.value);
  const handlePasswordChange = (event) => setPassword(event.target.value);
  const handleChange = (value) => setText(value);

  const API_BASE_URL = process.env.REACT_APP_API_BASE_URL;

  //  :    URL    
  const imageHandler = async () => {
    const input = document.createElement("input");
    input.setAttribute("type", "file");
    input.setAttribute("accept", "image/*");
    input.click();

    input.addEventListener("change", async () => {
      const file = input.files?.[0];
      if (file) {
        try {
          // FormData    
          const formData = new FormData();
          formData.append("image", file);

          //   API 
          const response = await axios.post(
            `${API_BASE_URL}/api/image/upload`,
            formData,
            {
              headers: { "Content-Type": "multipart/form-data" },
            }
          );

          //    URL  
          const imageUrl = response.data.url;
          const editor = quillRef.current.getEditor();
          const range = editor.getSelection();
          editor.insertEmbed(range.index, "image", imageUrl);
        } catch (error) {
          console.error("Image upload failed:", error);
          alert("  .");
        }
      }
    });
  };

  const modules = useMemo(
    () => ({
      toolbar: {
        container: [
          ["image"],
          [{ header: [1, 2, 3, 4, 5, false] }],
          ["bold", "underline"],
        ],
        handlers: { image: imageHandler },
      },
    }),
    []
  );

  //     
  const handleSave = async () => {
    if (!password.trim()) {
      alert(" .");
      return;
    }

    try {
      //   
      const passwordResponse = await axios.post(
        `${API_BASE_URL}/api/board/check-password`,
        {
          password,
          action: "write",
        }
      );

      if (passwordResponse.data.success) {
        //     
        const ptageBlock = text.replace(/<\/?p>/g, "");
        const formData = new FormData();

        const postData = {
          title: title,
          content: ptageBlock,
        };

        formData.append(
          "data",
          new Blob([JSON.stringify(postData)], { type: "application/json" })
        );
        formData.append("password", password);
        imageFiles.forEach((file) => formData.append("images", file));

        const response = await axios.post(
          `${API_BASE_URL}/api/board/save`,
          formData,
          {
            headers: { "Content-Type": "multipart/form-data" },
          }
        );

        if (response.status === 200) {
          alert(" .");
          navigate("/BoardList");
        } else {
          alert("  .");
        }
      } else {
        alert("  .");
      }
    } catch (error) {
      console.error("Error saving content:", error);
      alert("   .");
    }
  };

  return (
    <div id={styled.wrap}>
      <form
        className={styled.in_editor_form}
        onSubmit={(e) => {
          e.preventDefault();
          handleSave();
        }}
      >
        <div className={styled.title_info_titles}>
          <span></span>&nbsp;&nbsp;
          <input
            type="text"
            className={styled.in_editor_titleInput}
            value={title}
            onChange={handleTitleChange}
          />
        </div>
        <div className={styled.password_info}>
          <span></span>&nbsp;&nbsp;
          <input
            type="password"
            value={password}
            onChange={handlePasswordChange}
          />
        </div>
        <ReactQuill
          ref={quillRef}
          className={styled.in_editor}
          value={text}
          onChange={handleChange}
          modules={modules}
        />
        <button type="submit" className={styled.saveButton}>
           
        </button>
      </form>
    </div>
  );
}

export default BoardSave;
